<template>
  <div>
    <v-card
        v-if="status === 'approved'"
        color="transparent"
        flat="false"
    >
      <v-card-title class="white--text">Su pago fue aprovado</v-card-title>
      <v-card-subtitle class="white--text">En breve recibirá un correo de confirmación.</v-card-subtitle>
      <v-card-text class="white--text">Método de pago: {{payment_type}}</v-card-text>
      <v-card-text class="white--text">External references: {{external_reference}}</v-card-text>
      <v-card-text class="white--text">ID de Pago: {{payment_id}}</v-card-text>
    </v-card>
    <v-card
        v-if="status === 'denied'"
        color="transparent"
        flat="false"
    >
      <v-card-title class="white--text">Su pago fue rechazado</v-card-title>
      <v-card-subtitle class="white--text">Por favor inténtelo nuevamente.</v-card-subtitle>
    </v-card>
    <v-card
        v-if="status === 'in_process'"
        color="transparent"
        flat="false"
    >
      <v-card-title class="white--text">Su pago está en proceso</v-card-title>
      <v-card-subtitle class="white--text">Se le notificará cuando el pago se haya realizado de manera exitosa.</v-card-subtitle>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "Order",
  data (){
    return{
      status: this.$route.query.status,
      payment_type: this.$route.query.payment_type,
      external_reference: this.$route.query.external_reference,
      payment_id: this.$route.query.payment_id,
    }
  }
}
</script>

